import React, { useState } from 'react';
import axios from 'axios';

function LoginForm() {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [message, setMessage] = useState('');

    const handleSubmit = async (e) => {
        e.preventDefault();
        const response = await fetch('http://localhost:3000/api/login', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ email, password }) // Uywamy 'email' zamiast 'username'
        });
        const data = await response.json();
        if (response.ok) {
            localStorage.setItem('token', data.token); // Zapisujemy token w localStorage
            window.location.href = '/dashboard'; // Przekierowanie po zalogowaniu
        } else {
            setMessage(data.message);
        }
    };

    return (
        <form onSubmit={handleSubmit}>
            <div>
                <label>Email lub Nazwa uytkownika:</label>
                <input type="text" value={email} onChange={(e) => setEmail(e.target.value)} required />
            </div>
            <div>
                <label>Haso:</label>
                <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} required />
            </div>
            <button type="submit">Zaloguj si</button>
            {message && <p>{message}</p>}
        </form>
    );
}

export default LoginForm;
