import React, { useState } from 'react';
import axios from 'axios';

function RegisterForm() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [role, setRole] = useState('user');
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();
	 console.log("Dane formularza:", { username, password, role, email }); // Dodaj to logowanie
    axios.post('http://localhost:3000/api/auth/register', { username, password, role, email })
       .then(res => setMessage(res.data.message || "Rejestracja udana!"))
      .catch(err => {
        if (err.response) {
          // Jeli odpowied serwera zawiera dane
          setMessage(err.response.data.message || "Nieznany bd");
        } else {
          // Jeli nie ma odpowiedzi serwera, ale jest bd poczenia
          setMessage("Bd poczenia z serwerem");
        }
      });
  };

  return (
    <form onSubmit={handleSubmit}>
      <div>
        <label>Nazwa uytkownika:</label>
        <input type="text" name="username" value={username} onChange={(e) => setUsername(e.target.value)} required />
      </div>
      <div>
        <label>Email:</label>
        <input type="email" name="email" value={email} onChange={(e) => setEmail(e.target.value)} required />
      </div>
      <div>
        <label>Haso:</label>
        <input type="password" name="password" value={password} onChange={(e) => setPassword(e.target.value)} required />
      </div>
      <div>
        <label>Rola:</label>
        <select name="role" value={role} onChange={(e) => setRole(e.target.value)} required>
          <option value="user">Uytkownik</option>
          <option value="admin">Admin</option>
        </select>
      </div>
      <button type="submit">Zarejestruj si</button>
      {message && <p>{message}</p>}
    </form>
  );
}

export default RegisterForm;
