import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { jwtDecode } from 'jwt-decode';


import ServicesList from '../components/ServicesList';
import EmployeesList from '../components/EmployeesList';
import AppointmentsList from '../components/AppointmentsList';
import ReviewsList from '../components/ReviewsList';
import StatsDashboard from '../components/StatsDashboard';

function Dashboard() {
  const history = useHistory();
  const [role, setRole] = useState("");
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(""); // Stan bdu

  useEffect(() => {
    const token = localStorage.getItem("token");
    if (!token) {
      history.push("/login"); // Przekierowanie na login, jeli brak tokena
      return;
    }

    try {
      const decodedToken = jwtDecode(token); // Dekodowanie tokena
      setRole(decodedToken.role);
    } catch (error) {
      console.error("Bd dekodowania tokena:", error);
      history.push("/login"); // Przekierowanie na login w przypadku bdu
    } finally {
      setLoading(false); // Zakoczenie adowania po dekodowaniu tokena
    }
  }, [history]);

  // Jeli dane si aduj, wywietl loading spinner lub komunikat
  if (loading) {
    return <p>adowanie danych...</p>;
  }

  return (
    <div>
      <h2>Dashboard</h2>
      {role === "admin" && (
        <>
          <h3>Statystyki i zarzdzanie</h3>
          <ServicesList />
          <EmployeesList />
          <AppointmentsList />
          <ReviewsList />
          <StatsDashboard />
        </>
      )}
      {role === "user" && (
        <p>Witaj, uytkowniku! Masz ograniczony dostp do funkcji.</p>
      )}
      {role !== "admin" && role !== "user" && (
        <p>Brak dostpu. Prosz skontaktowa si z administratorem.</p>
      )}
    </div>
  );
}

export default Dashboard;
